<template>
	<view class="login-container">
		<!-- Áä∂ÊÄÅÊ†èÂç†‰Ωç -->
		<view class="status-bar" :style="{height: statusBarHeight + 'px'}"></view>
		
		<!-- ËøîÂõûÊåâÈíÆ -->
		<view class="back-btn" @click="goBack" v-if="canGoBack">
			<text class="back-icon">‚Äπ</text>
		</view>
		
		<!-- LogoÂå∫Âüü -->
		<view class="logo-section">
			<view class="logo-container">
				<view class="k-logo">K</view>
			</view>
			<view class="welcome-text">Âó®ÔºåÊàëÊòØÂïÜKÈ¢ÑÁ∫¶</view>
			<view class="description-container">
				<text class="description">Âú®ËøôÈáå‰Ω†ÂèØ‰ª•ÊêúÁ¥¢ÈôÑËøëÁöÑÂïÜK</text>
				<text class="description">Âπ∂Áî±ÂÆ¢ÊúçÊåâÁÖß‰Ω†ÁöÑÂñúÂ•ΩÈ¢ÑÁ∫¶</text>
			</view>
		</view>
		
		<!-- ÁôªÂΩïË°®ÂçïÂç°Áâá -->
		<view class="login-card">
			<view class="login-form">
				<view class="form-title">È™åËØÅÁ†ÅÁôªÂΩï</view>
				
				<view class="input-container">
					<view class="phone-input-wrapper">
						<view class="phone-icon">üì±</view>
						<input 
							class="phone-input" 
							v-model="phone" 
							placeholder="ËØ∑ËæìÂÖ•‰Ω†ÁöÑÊâãÊú∫Âè∑" 
							type="number"
							maxlength="11"
						/>
					</view>
				</view>
				
				<view class="code-input-group" v-if="showCodeInput">
					<text class="code-label">ËØ∑ËæìÂÖ•È™åËØÅÁ†Å</text>
					<text class="code-sent">È™åËØÅÁ†ÅÂ∑≤ÈÄöËøáÁü≠‰ø°ÂèëÈÄÅËá≥Ôºö</text>
					<text class="phone-display">+86 {{ phone }}</text>
					<button class="resend-btn" @click="resendCode">ÈáçÊñ∞Ëé∑Âèñ</button>
					
					<view class="code-inputs">
						<input 
							v-for="(item, index) in codeArray" 
							:key="index"
							class="code-digit" 
							v-model="codeArray[index]" 
							type="number" 
							maxlength="1"
							@input="onCodeInput(index, $event)"
						/>
					</view>
				</view>
				
				<button 
					class="main-btn" 
					:class="{ 'disabled': !canSubmit }" 
					@click="handleLogin"
					:disabled="!canSubmit"
				>
					{{ showCodeInput ? 'ÁôªÂΩï' : 'Ëé∑ÂèñÈ™åËØÅÁ†Å' }}
				</button>
			</view>
			
			<!-- ÂçèËÆÆ -->
			<view class="agreement">
				<view class="agreement-item" @click="toggleAgreement">
					<view class="checkbox" :class="{ 'checked': agreed }">
						<text class="checkmark" v-if="agreed">‚úì</text>
					</view>
					<text class="agreement-text">ÊàëÂ∑≤ÈòÖËØªÂπ∂ÂêåÊÑè„ÄäÁî®Êà∑ÂçèËÆÆ„ÄãÂíå„ÄäÈöêÁßÅÊîøÁ≠ñ„Äã</text>
				</view>
			</view>
			
			<!-- Â∫ïÈÉ® -->
			<view class="footer">
				<text class="footer-text">ÂÆâÂÖ® ÈÄèÊòé Êúâ‰øùÈöú</text>
				<view class="apple-logo">
					<text class="apple-icon"></text>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
export default {
	data() {
		return {
			statusBarHeight: 0,
			phone: '',
			codeArray: ['', '', '', ''],
			showCodeInput: false,
			agreed: true,
			countdown: 0,
			timer: null,
			canGoBack: false
		}
	},
	computed: {
		canSubmit() {
			if (!this.showCodeInput) {
				return this.phone.length === 11 && this.agreed;
			} else {
				return this.codeArray.every(code => code !== '') && this.agreed;
			}
		}
	},
	onLoad() {
		// Ëé∑ÂèñÁä∂ÊÄÅÊ†èÈ´òÂ∫¶
		try {
			// ‰ºòÂÖà‰ΩøÁî®Êñ∞ÁöÑAPI
			if (uni.getWindowInfo) {
				const windowInfo = uni.getWindowInfo();
				this.statusBarHeight = windowInfo.statusBarHeight;
			} else {
				// ÂÖºÂÆπÊóßÁâàÊú¨
				const systemInfo = uni.getSystemInfoSync();
				this.statusBarHeight = systemInfo.statusBarHeight;
			}
		} catch (error) {
			console.log('Ëé∑ÂèñÁ≥ªÁªü‰ø°ÊÅØÂ§±Ë¥•Ôºå‰ΩøÁî®ÈªòËÆ§ÂÄº');
			this.statusBarHeight = 44; // ÈªòËÆ§Áä∂ÊÄÅÊ†èÈ´òÂ∫¶
		}
		
		// Ê£ÄÊü•ÊòØÂê¶ÂèØ‰ª•ËøîÂõû
		const pages = getCurrentPages();
		this.canGoBack = pages.length > 1;
	},
	methods: {
		goBack() {
			uni.navigateBack();
		},
		
		async handleLogin() {
			if (!this.canSubmit) return;
			
			if (!this.showCodeInput) {
				// ÂèëÈÄÅÈ™åËØÅÁ†Å
				await this.sendCode();
			} else {
				// È™åËØÅÁôªÂΩï
				await this.login();
			}
		},
		
		async sendCode() {
			try {
				// Ë∞ÉÁî®ÁúüÂÆûÁöÑÁü≠‰ø°API
				const res = await uni.request({
					url: 'http://catdog.dachaonet.com/send_code.php',
					method: 'POST',
					header: {
						'Content-Type': 'application/json'
					},
					data: {
						phone: this.phone
					}
				});
				
				if (res.data.success) {
					this.showCodeInput = true;
					this.startCountdown();
					
					uni.showToast({
						title: 'È™åËØÅÁ†ÅÂ∑≤ÂèëÈÄÅ',
						icon: 'success'
					});
				} else {
					throw new Error(res.data.message || 'ÂèëÈÄÅÂ§±Ë¥•');
				}
				
				// ÊòæÁ§∫ÊèêÁ§∫
				setTimeout(() => {
					uni.showToast({
						title: 'ÊµãËØïÈ™åËØÅÁ†ÅÔºö1234',
						icon: 'none',
						duration: 3000
					});
				}, 1500);
				
			} catch (error) {
				console.error('ÂèëÈÄÅÈ™åËØÅÁ†ÅÂ§±Ë¥•:', error);
				uni.showToast({
					title: 'ÁΩëÁªúÈîôËØØ',
					icon: 'none'
				});
			}
		},
		
		async login() {
			try {
				const code = this.codeArray.join('');
				
				if (!code || code.length !== 6) {
					uni.showToast({
						title: 'ËØ∑ËæìÂÖ•6‰ΩçÈ™åËØÅÁ†Å',
						icon: 'none'
					});
					return;
				}
				
				// Ë∞ÉÁî®ÁúüÂÆûÁöÑÁôªÂΩïAPI
				const res = await uni.request({
					url: 'http://catdog.dachaonet.com/login.php',
					method: 'POST',
					header: {
						'Content-Type': 'application/json'
					},
					data: {
						phone: this.phone,
						code: code
					}
				});
				
				if (res.data.success) {
					// ‰øùÂ≠òÁî®Êà∑‰ø°ÊÅØÂíåtoken
					uni.setStorageSync('userInfo', res.data.data.user);
					uni.setStorageSync('token', res.data.data.token);
				} else {
					uni.showToast({
						title: res.data.message || 'ÁôªÂΩïÂ§±Ë¥•',
						icon: 'none'
					});
					return;
				}
				
				uni.showToast({
					title: 'ÁôªÂΩïÊàêÂäü',
					icon: 'success'
				});
				
				// Ë∑≥ËΩ¨Âà∞È¶ñÈ°µ
				setTimeout(() => {
					uni.switchTab({
						url: '/pages/index/index'
					});
				}, 1500);
				
			} catch (error) {
				console.error('ÁôªÂΩïÂ§±Ë¥•:', error);
				uni.showToast({
					title: 'ÁΩëÁªúÈîôËØØ',
					icon: 'none'
				});
			}
		},
		
		resendCode() {
			if (this.countdown > 0) return;
			this.sendCode();
		},
		
		startCountdown() {
			this.countdown = 60;
			this.timer = setInterval(() => {
				this.countdown--;
				if (this.countdown <= 0) {
					clearInterval(this.timer);
				}
			}, 1000);
		},
		
		onCodeInput(index, event) {
			const value = event.detail.value;
			this.codeArray[index] = value;
			
			// Ëá™Âä®Ë∑≥ËΩ¨Âà∞‰∏ã‰∏Ä‰∏™ËæìÂÖ•Ê°Ü
			if (value && index < 3) {
				const nextInput = document.querySelectorAll('.code-digit')[index + 1];
				if (nextInput) {
					nextInput.focus();
				}
			}
		},
		
		toggleAgreement() {
			this.agreed = !this.agreed;
		}
	},
	
	onUnload() {
		if (this.timer) {
			clearInterval(this.timer);
		}
	}
}
</script>

